body{
  background-color:#111827
}

.App{
  min-height:100vh
}

.App .Toastify__toast--success{
  background:#5961ab
}

.discord-st0{
  fill:#a78bfa
}

hr.styled-hr{
  border-top:1px solid #0f131a
}

.nav-link{
  transition:0.3s
}

.nav-link:hover{
  color:rgba(255,255,255,0.8)
}

.nav-link.nav-link-hamburger:hover{
  background-color:rgba(255,255,255,0.1)
}

.bg-header{
  background-color:#0b0f19
}

.app-burger .bm-burger-button{
  position:relative;
  width:36px;
  height:30px
}

.app-burger .bm-burger-bars{
  background:#fff
}

.app-burger .bm-burger-bars-hover{
  background:rgba(255,255,255,0.8)
}

.app-burger .bm-cross-button{
  height:36px !important;
  width:36px !important
}

.app-burger .bm-cross{
  background:#bdc3c7
}

.app-burger .bm-menu-wrap{
  position:fixed;
  right:0px;
  z-index:1100;
  width:300px;
  height:100%;
  transition:all 0.5s ease 0s;
  top:0px
}

.app-burger .bm-menu{
  box-shadow:-20px -1px 20px 9px rgba(0,0,0,0.34);
  background:#0b0f19;
  padding:2.5em 0;
  font-size:1.15em;
  border-left:2px solid #374151
}

.app-burger .bm-morph-shape{
  fill:#373a47
}

.app-burger .bm-item-list{
  color:#b8b7ad;
  padding:0.8em 0
}

.app-burger .bm-item{
  display:inline-block
}

.app-burger .bm-overlay{
  background:rgba(0,0,0,0.3)
}

.item-tree{
  width:600px
}

.item-tree .item-container{
  height:70px;
  padding-top:10px
}

.item-tree .item{
  display:-webkit-flex;
  display:flex;
  height:100%;
  border-top-left-radius:3px;
  border-bottom-left-radius:3px;
  font-size:13px;
  width:310px
}

.item-tree .item .item-icon>img{
  border-top-left-radius:3px;
  border-bottom-left-radius:3px;
  height:54px;
  width:54px;
  object-fit:cover
}

.item-tree .item .item-name-price{
  border-right:3px solid #212935;
  padding:0rem 0.5rem 0rem 0.5rem;
  min-width:223px
}

.item-tree .item .item-name-container{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
  align-items:center;
  overflow:hidden;
  white-space:nowrap;
  text-overflow:ellipsis
}

.item-tree .item .item-action-container{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
  flex-direction:column;
  -webkit-justify-content:space-between;
  justify-content:space-between;
  width:27px
}

.item-tree .item .item-action-container>*:first-child{
  padding-top:2px;
  -webkit-flex:1 1;
  flex:1 1
}

.item-tree .item .item-action-container>*:last-child{
  padding-top:5px;
  -webkit-flex:1 1;
  flex:1 1
}

.item-tree .item .item-action-container button{
  height:18px
}

.item-tree .item .item-action-container a{
  height:18px
}

.item-tree .item svg{
  width:18px
}

.item-tree .item .item-name{
  overflow:hidden;
  white-space:nowrap;
  text-overflow:ellipsis;
  max-width:180px
}

.item-tree .item .item-name-copy-container{
  opacity:1;
  color:#45546e
}

.item-tree .item:hover .item-name-copy-container:focus{
  outline:none
}

.item-tree .item button.item-name-copy-container:hover{
  background:rgba(0,0,0,0.5);
  transition:0.3s;
  color:#a78bfa
}

.item-tree .item a.item-name-copy-container:hover{
  background:rgba(0,0,0,0.5);
  transition:0.3s;
  color:#a78bfa
}

.item-tree .item .price-note{
  color:#c2c2c2;
  font-size:11px
}

.item-tree .item.item-dotted{
  border:dotted #2d3748 3px;
  border-radius:3px;
  background:#141c2e
}

.item-tree .item.item-dotted .item-name-price{
  border-style:dotted
}

.item-tree .item.item-solid{
  border:solid #2d3748 3px;
  border-radius:3px;
  background:#141c2e
}

.item-tree .item.item-solid .item-name-price{
  border-style:solid
}

.formatted-gold-container{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
  align-items:center;
  font-size:11px
}

.formatted-gold-container img{
  height:10px;
  margin-right:3px
}

