  background-color:#3d4375;
  padding:2px
}

.expand-tree-icon-container{
  width:0px
}

.expand-tree-icon{
  height:23px;
  position:relative;
  bottom:0px;
  left:-40px;
  border-radius:32px;
  background:#1a202c;
  z-index:2;
  border:2px solid #1a202c
}

.expand-tree-icon:focus{
  background:#53668d;
  transition:0.3s
}

.expand-tree-icon:hover{
  background:#53668d;
  transition:0.3s
}

hr.custom{
  height:1px;
  background-color:#1f2c47;
  border:none
}

.search-form{
  border:1px solid #1f2c47;
  border-radius:6px;
  background-color:#141c2e
}

.search-form button.search-button:focus{
  box-shadow:0 0 0 2px #fff
}

.search-form .search-icon-container{
  width:0;
  position:relative;
  z-index:2;
  top:7px;
  right:-10px;
  color:#bdbdbd;
  pointer-events:none
}

.search-form .search-icon-container i.fas.fa-search{
  font-size:19px
}

.search-form input.search-query{
  height:36px;
  border:1px solid #1f2c47;
  border-radius:3px;
  padding:2px 37px;
  background:#0b0f19;
  color:#fff;
  font-size:1rem
}

.search-form input.search-query:hover{
  border:1px solid #5878b6
}

.search-form input.search-query:focus{
  box-shadow:0 0 0 1px #2684ff;
  outline:none
}

.search-form .field-validation-container{
  min-height:30px;
  padding:3px 4px;
  color:#EF4444
}

.search-form .radio-selection{
  border:1px solid #1f2c47;
  border-radius:5px;
  color:#fff
}

.search-form .radio-selection:focus-within{
  box-shadow:0 0 0 1px #fff
}

.search-form .radio-selection input[type="radio"]{
  opacity:0;
  height:0;
  position:absolute
}

.search-form .radio-selection input[type="radio"]:not(:disabled) ~ label{
  cursor:pointer
}

.search-form .radio-selection input[type="radio"]:disabled ~ label{
  color:#bcc2bf;
  border-color:#bcc2bf;
  box-shadow:none;
  cursor:not-allowed
}

.search-form .radio-selection label{
  display:block;
  background:#111827;
  border-radius:4px;
  padding:1rem 1rem 1rem 3rem;
  position:relative
}

.search-form .radio-selection input[type="radio"]:checked+label{
  background:#6d28d9;
  color:#fff;
  transition:0.3s
}

.search-form .radio-selection input[type="radio"]:checked+label .radio-subtext{
  color:#d8c7f5
}

.search-form .radio-selection input[type="radio"]:checked+label::after{
  border:5px solid #ffffff;
  content:"";
  position:absolute;
  top:19px;
  left:24px;
  -webkit-transform:translateX(-50%);
  transform:translateX(-50%);
  height:20px;
  width:20px;
  line-height:50px;
  text-align:center;
  border-radius:50%;
  background:#4f1ca0
}

.search-form .radio-selection input[type="radio"]:not(:checked)+label{
  transition:0.3s
}

.search-form .radio-selection input[type="radio"]:not(:checked)+label .radio-subtext{
  color:#9ca3af
}

.search-form .radio-selection input[type="radio"]:not(:checked)+label:hover{
  background-color:#1b1d3a
}

.search-form .radio-selection input[type="radio"]:not(:checked)+label::after{
  border:2px solid #ffffff;
  content:"";
  position:absolute;
  top:19px;
  left:24px;
  -webkit-transform:translateX(-50%);
  transform:translateX(-50%);
  height:20px;
  width:20px;
  line-height:50px;
  text-align:center;
  border-radius:50%;
  background:#111827
}

